.edm-toast{position:absolute;top:50%;left:50%;max-width:18.75rem;padding:.9375rem 1.25rem;font-size:.875rem;line-height:1.2;color:#fff;text-align:center;background:rgba(0,0,0,.7);border-radius:.25rem;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%)}.edm-toast>svg{width:2.5rem;height:2.5rem}