.edm-messagebox{position:absolute;top:50%;left:50%;width:18.75rem;max-width:18.75rem;overflow:hidden;font-size:.875rem;line-height:1.2;text-align:center;background:#fff;border-radius:.25rem;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);-webkit-box-sizing:border-box;box-sizing:border-box}.edm-messagebox-close{position:absolute;top:.625rem;right:.625rem;z-index:1}.edm-messagebox-title{position:relative;margin:1.25rem;font-size:1rem;font-weight:700;line-height:1.5}.edm-messagebox-content{position:relative;max-height:56vh;margin:1.25rem;overflow:scroll;color:#666}.edm-messagebox-line{background-image:-webkit-gradient(linear,left bottom,left top,from(#e5e5e5),to(#fff));background-image:-webkit-linear-gradient(bottom,#e5e5e5,#fff);background-image:linear-gradient(bottom,#e5e5e5,#fff);background-repeat:no-repeat;background-size:auto 1PX;height:1PX}.edm-messagebox-buttons{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-flex-wrap:nowrap;-ms-flex-wrap:nowrap;flex-wrap:nowrap;-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-webkit-flex-direction:row-reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse}.edm-messagebox-buttons-multiline{display:block;padding:0 1.25rem;background-image:none}.edm-messagebox-buttons-multiline-last{color:#666;background:none;border:none}.edm-messagebox-buttons-multiline-alert{padding-bottom:1.25rem}